.overlay {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgb(0, 0, 0);
  z-index: 1000;
  transition: .3s ease-in-out;
  display: flex;
  justify-content: center;
  align-items: center;
}

.overlayFade {
  composes: overlay;
  opacity: 0;
}

.uxContainer {
  position: absolute;
  width: 100%;
  height: 100vh;
  transition: .2s ease-in-out;
}

.uxContainerHidden {
  composes: uxContainer;
  opacity: 0;
  visibility: hidden;
}

.closeButton {
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 10;
  background-color: transparent;
  border: none;
  background-image: url(../../images/close.svg);
  background-color: white;
  border-radius: 10px;
  overflow: hidden;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 30px;
  height: 30px;
  z-index: 10;
  padding: 0;
  cursor: pointer;
}

.flexContainer {
  width: 100vw;
  height: 100vh;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.imageContainer {
  width: max-content;
  width: 100vw;
  height: 100vh;
  max-height: max-content;
  position: relative;
  transition: .2s ease-in-out;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 5;
  /* overflow: scroll; */
  /* top: -50px; */
}

.imagesContainer {
  display: flex;
  overflow-x: scroll;
  overflow-y: hidden;
  gap: 20px;
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;

}

.imagesContainer::-webkit-scrollbar {
  display: none;
}

.image {
  width: 100%;
  max-height: 100vh;
  object-fit: contain;
  display: block;
  overflow: scroll;
}

.imageContainer::-webkit-scrollbar {
  display: none;
}

.imageContainerFade {
  composes: imageContainer;
  opacity: 0;
}

.description {
  position: absolute;
  bottom: 30px;
  left: 30px;
  transition: .2s ease-in-out;
  z-index: 10;
}

.name {
  font-size: 20px;
  font-weight: 700;
  text-shadow: 0px 0px 1px white;
  margin: 0 0 10px 0;
}

.category {
  background-color: white;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 19px;
  padding: 3px 16px 2px 16px;
  font-size: 16px;
  line-height: 32px;
  color: #391400;
  margin: 0;
}

.imageContainer:hover > .description {
  opacity: 1;
}


.buttonPrev {
  border: none;
  outline: none;
  background-color: transparent;
  background-image: url(../../images/arrowWhite.png);
  width: 50px;
  height: 50px;
  position: absolute;
  top: 50%;
  left: 10px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  cursor: pointer;
  border-radius: 10px;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  z-index: 10;
}

.buttonNext {
  /* composes: buttonPrev; */
  border: none;
  outline: none;
  background-color: transparent;
  width: 50px;
  height: 50px;
  position: absolute;
  top: 50%;
  right: 10px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  cursor: pointer;
  border-radius: 10px;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  background-image: url(../../images/arrowWhiteRight.png);
  z-index: 10;
}

.buttonPrevAnimate {
  composes: buttonPrev;
  animation: pushButton .2s ease-in-out;
}

.buttonNextAnimate {
  composes: buttonNext;
  animation: pushButton .2s ease-in-out;
}

@keyframes pushButton {
  0% {
    transform: scale(.9);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}

.cardsConteiner {
  position: absolute;
  display: flex;
  /* gap: 10px; */
  bottom: 0px;
  left: 50%;
  transform: translate(-50%);
  overflow: scroll;
  max-width: 70%;
  margin: 0 auto;
  align-items: center;
  scrollbar-width: 0;
  justify-content:space-between;
  z-index: 10;
}

.cardsConteiner::-webkit-scrollbar {
  display: none;
}

@media screen and (max-width: 500px) and (max-height: 900px) {
  .buttonPrev {
    position: absolute;
    top: initial;
    bottom: 5px;
    left: 20px;
    width: 40px;
    height: 40px;
  }

  .buttonNext {
    position: absolute;
    top: initial;
    bottom: 5px;
    right: 20px;
    width: 40px;
    height: 40px;
  }

  .cardsConteiner {
    width: 60%;
  }

  .description {
    bottom: 10px;
    left: 10px;
  }

  .name {
    font-size: 15px;
  }

  .category {
    font-size: 10px;
  }
}

@media screen and (max-width: 900px) and (max-height: 500px) {
  .buttonPrev {
    position: absolute;
    top: initial;
    bottom: 5px;
    left: 20px;
    width: 40px;
    height: 40px;
  }

  .buttonNext {
    position: absolute;
    top: initial;
    bottom: 5px;
    right: 20px;
    width: 40px;
    height: 40px;
  }
}

